* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto Slab";
}
/* body {
  font-family: "Roboto Slab";
  background: white;
} */
/*------------global-----------*/
.flexSB {
  display: flex;
  justify-content: space-between;

  /* text-align: center; */
}
p {
  line-height: 30px;
  font-size: 18px;
}

.icon {
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.3);
  /* color: #1eb2a6; */
  color: #0000cd;
  transition: 0.5s;
}
.icon:hover {
  cursor: pointer;
  background-color: #0000cd;
  /* background-color: #1eb2a6; */
  color: #fff;
}
a {
  text-decoration: none;
}
li {
  list-style-type: none;
}
.row-srt {
  width: 50%;
}
button {
  padding: 17px 30px;
  background-color: #fff;
  color: #0000cd;
  /* color: #05000a; */
  /* color: #1eb2a6; */
  font-weight: 600;
  border: none;
  margin: 30px 10px 0 0;
  border-radius: 3px;
  cursor: pointer;
  box-shadow: 0 24px 36px -11px rgb(0 0 0 / 9%);
  transition: 0.5s;
}
button i {
  margin-left: 20px;
}
button:hover {
  box-shadow: none;
}
.primary-btn {
  background-color: #0000cd;
  /* background-color: #1eb2a6; */
  color: #fff;
}

.back {
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100vh;
  padding-top: 30%;
  padding-right: 50px;
  color: #fff;
  text-align: center;
}
.back h1 {
  font-size: 100px;
  font-weight: 400;
}
.back h2 {
  font-weight: 500;
  font-size: 17px;
  text-transform: uppercase;
}
.margin {
  margin-top: 40.3%;
}
.grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 30px;
}
.flex {
  display: flex;
}
.grid2 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
}
.outline-btn {
  margin: 0;
  box-shadow: none;
  border: 2px solid #0000cd;
  width: 100%;
  transition: 0.5s;
}
.outline-btn:hover {
  background-color: #0000cd;
  /* background-color: #1eb2a6; */
  color: #fff;
}
.grid3 {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 10px;
}
.padding {
  padding: 80px 0;
}
.shadow {
  box-shadow: 0 5px 25px -2px rgb(0 0 0 / 6%);
  background-color: #fff;
}
@media screen and (max-width: 1200px) {
  .grid3,
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }

  /* .grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 30px;
  } */
  /* .flex {
    display: flex;
  } */
  .grid2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
}
@media screen and (max-width: 768px) {
  .grid2,
  .grid3,
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
  .back {
    background-position: center;
    padding-top: 50%;
    height: 80vh;
  }
  #heading h3 {
    font-weight: 600;
    letter-spacing: 1px;
    color: #0000cd;
    /* color: #1eb2a6; */
    text-transform: uppercase;

    width: 80%;
  }
  #heading h1 {
    font-size: 42px;
    margin: 20px 0;
    text-transform: capitalize;
    width: 120%;
  }

  .grid2 {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 30px;
  }
}
@media screen and (max-width: 480px) {
  #heading h3 {
    font-weight: 600;
    letter-spacing: 1px;
    color: #0000cd;
    /* color: #1eb2a6; */
    text-transform: uppercase;

    width: 120%;
  }
  #heading h1 {
    font-size: 40px;
    margin: 20px 0;
    text-transform: capitalize;
    width: 140%;
  }
  p {
    line-height: 30px;
    font-size: 18px;
  }
  .grid2 {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 30px;
    justify-content: center;
  }
}

